import React from 'react';
import { Box, Container, Typography, Grid, IconButton, Paper } from '@mui/material';
import { Email, Phone, LinkedIn, GitHub, LocationOn } from '@mui/icons-material';
import { styled } from '@mui/material/styles';

const ContactSection = styled(Box)(({ theme }) => ({
  padding: theme.spacing(8, 0),
  backgroundColor: theme.palette.primary.main,
  color: theme.palette.primary.contrastText,
}));

const ContactCard = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(3),
  height: '100%',
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  backgroundColor: 'rgba(255, 255, 255, 0.9)',
  color: theme.palette.text.primary,
}));

const Contact = () => {
  return (
    <ContactSection id="contact">
      <Container maxWidth="lg">
        <Typography variant="h3" component="h2" align="center" gutterBottom>
          Get In Touch
        </Typography>
        <Grid container spacing={4} justifyContent="center">
          <Grid item xs={12} sm={6} md={4}>
            <ContactCard elevation={3}>
              <Email fontSize="large" color="primary" />
              <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>
                Email
              </Typography>
              <Typography align="center">
                mahaveersingh99297409@gmail.com
              </Typography>
            </ContactCard>
          </Grid>
          <Grid item xs={12} sm={6} md={4}>
            <ContactCard elevation={3}>
              <Phone fontSize="large" color="primary" />
              <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>
                Phone
              </Typography>
              <Typography>
                +91-6378271484
              </Typography>
            </ContactCard>
          </Grid>
          <Grid item xs={12} sm={6} md={4}>
            <ContactCard elevation={3}>
              <LocationOn fontSize="large" color="primary" />
              <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>
                Location
              </Typography>
              <Typography>
                Sambhar Lake, Rajasthan
              </Typography>
            </ContactCard>
          </Grid>
        </Grid>
        
        <Box sx={{ mt: 4, textAlign: 'center' }}>
          <IconButton
            href="https://www.linkedin.com/in/mahaveer-singh-20224524a/"
            target="_blank"
            rel="noopener noreferrer"
            sx={{ color: 'white', mx: 1 }}
          >
            <LinkedIn fontSize="large" />
          </IconButton>
          <IconButton
            href="https://github.com/MahaveerSingh2003"
            target="_blank"
            rel="noopener noreferrer"
            sx={{ color: 'white', mx: 1 }}
          >
            <GitHub fontSize="large" />
          </IconButton>
        </Box>
      </Container>
    </ContactSection>
  );
};

export default Contact; 